.App {
    background: url(./img/bgcLap.png) no-repeat;
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 0;
}

.content {
    width: 80%;
    height: 80%;
    margin: 25% 10%;
    opacity: 0.8;
    background-color: #000;
}

.logoWrapper {
    width: 100%;
    display: flex;
    justify-content: right;
    margin-bottom: 40px;
}

.tabs {
    width: 100%;
    display: flex;
    justify-content: space-around;
}

.btn {
    background-color: transparent;
    border: 0;
    color: #fff;
    width: 50%;
    height: 100%;
    padding: 20px 0;
    cursor: pointer;
}

.active {
    background-color: #fff;
    color: #000;
}

.logo {
    position: absolute;
    width: 200px;
    height: 140px;
    bottom: 400px;
    opacity: 0.3;
}

.title {
    color: #fff;
    font-size: 12px;
    margin: 0;
    margin-bottom: 10px;
}

.menu {
    width: 80%;
    height: 80%;
    overflow: auto;
    margin-left: 25px;
    padding-right: 20px;
}

.menuItem {
    margin-bottom: 20px;
}

.list {
    list-style: none;
    margin: 0 0 0 10px;
    padding: 0;
}

.item {
    display: flex;
    justify-content: space-between;
    margin-bottom: 5px;
    font-size: 10px;
    color: #fff;
}

.ml {
    margin-right: 15px;
}
